@import url('https://fonts.googleapis.com/css?family=Montserrat');


* {
	box-sizing: border-box;
	
}

body {
	font-family: Montserrat, sans-serif;
	/* background-image: linear-gradient(to bottom right, blue, pink); */
	display: flex;
	justify-content: center;
	flex-direction: column;
	min-height: 100vh;
	margin: 0;
	background-repeat: no-repeat; 
    background-position: center;
    background-attachment: fixed;       
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    height:100%;
    width:100%; 
}

h3 {
	margin: 10px 0;
}

h6 {
	margin: 5px 0;
	text-transform: uppercase;
}

p {
	font-size: 14px;
	line-height: 21px;
}

.card-container {
	background-image: linear-gradient( 111.5deg, rgba(187,232,255,1) 11.1%, rgba(4,78,116,1) 107.5% );
	border-radius: 25px;
	box-shadow: 10px 10px 15px rgba(0,0,0,0.35);
	color: #1a1e2f;
	padding: 30px 0 0;
	position: relative;
	width: 500px;
	max-width: 100%;
	text-align: center;
	margin: 105px 0;
	overflow: hidden;
}


.card-container .pro {
	color: #231E39;
	background-color: #FEBB0B;
	border-radius: 3px;
	font-size: 14px;
	font-weight: bold;
	padding: 3px 7px;
	position: absolute;
	top: 30px;
	left: 30px;
}

.card-container img {
	border: 1px solid#1c243c;
	border-radius: 50%;
	padding: 7px;
	height: 150px;
	width: 150px;
}

button.primary {
	background-color: #C400D7;
	border: 1px solid #C400D7;
	border-radius: 3px;
	color: #fff;
	font-family: Montserrat, sans-serif;
	font-weight: 500;
	padding: 10px 25px;
}

button.primary.ghost {
	background-color: transparent;
	color: #C400D7;
}

.skills {
	background-image: linear-gradient( 111.5deg, rgba(187,232,255,1) 11.1%, rgba(4,78,116,1) 107.5% );
	text-align: left;
	padding: 15px;
	margin-top: 30px;
}

.skills ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.skills ul li {
	border: 1px solid #2D2747;
	border-radius: 2px;
	display: inline-block;
	font-size: 12px;
	margin: 0 7px 7px 0;
	padding: 7px;
}


